#hero-home {
    width: 100%;
    position: relative;
    min-height: 137vh;
    background-image: url('../img/hero-bg.gif');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    #hero-home-animation {
        width: 100%;
        position: absolute;
    }

    #hero {
        padding-top: 120px;

        .top {
            h1 {
                font-family: "Champ";
                font-weight: 900;
                font-size: 60px;
                line-height: 60px;
                letter-spacing: 0.03em;
                color: #ffffff;
                margin-bottom: 25px;
            }

            span {
                font-family: "Poppins";
                font-weight: 400;
                font-size: 18px;
                line-height: 27px;
                text-align: center;
                letter-spacing: 0.01em;
                color: #ffffff;
            }
        }

        .bottom {
            margin-top: 25px;

            .btn-hero {
                padding: 16px 25px;
                gap: 12px;
                background: #1a0e3c;
                border-radius: 40px;
                font-family: "Poppins";
                font-weight: 900;
                font-size: 18px;
                line-height: 27px;
                letter-spacing: 0.02em;
                color: #ffffff;

                img {
                    margin-left: 12px;
                }
            }
        }
    }
}

#service{
    width: 100%;
    position: relative;
    background: #0f0b25;
    padding-top: 130px;
    padding-bottom: 130px;

    .title {
        font-family: "Champ";
        font-weight: 900;
        font-size: 50px;
        line-height: 54px;
        letter-spacing: 0.02em;
        color: #ffffff;

        img {
            margin-top: -10px;
            position: absolute;
            width: 260px;
            height: 123px;
        }

        p {
            display: none;
            font-family: 'Poppins';
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            letter-spacing: 0.01em;
            color: #FFFFFF;
        }
    }

    .text-innovate-right {
        font-family: "Poppins";
        font-weight: 400;
        font-size: 20px;
        line-height: 32px;
        letter-spacing: 0.01em;
        color: #ffffff;
        margin-bottom: 54px;
    }

    .card-innovate-left {
        margin-top: 120px;
        width: 100%;
        height: 634px;
        z-index: 1;
        overflow: hidden;
        transition: all .3s ease;

        .card-body {
            padding: 28px;
            overflow: hidden;
            z-index: 1;
        }
    }

    .card-innovate-right {
        width: 100%;
        height: 360px;
        z-index: 1;
        overflow: hidden;
        transition: all .3s ease;

        .desc {
            position: absolute;
            bottom: 30px;
        }
    }

    .card-innovate {
        background: #331d73;
        border-radius: 20px;
        background-size: cover;
         background-position: center;
         background-repeat: no-repeat;

        .card-body {
            padding: 30px;
            overflow: hidden;
            box-sizing: border-box;

            .desc {
                font-family: "Poppins";

                h3 {
                    margin-top: 6px;
                    font-weight: 700;
                    font-size: 30px;
                    line-height: 34px;
                    letter-spacing: 0.01em;
                    color: #ffffff;
                    margin-bottom: 12px;
                }

                p {
                    max-width: 100%;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: break-spaces;
                    text-align: left;
                    font-weight: 400;
                    font-size: 15px;
                    line-height: 24px;
                    color: #b9bae2;
                    margin-bottom: 0;
                }
            }
        }
    }
}

#workflow {
    width: 100%;
    position: relative;
    padding-top: 120px;
    padding-bottom: 36px;

    .title {
        font-family: "Champ";
        font-weight: 900;
        font-size: 56px;
        line-height: 62px;
        letter-spacing: 0.02em;
        color: #10121f;

        span {
            color: #613ad2;
        }
    }

    .desc,
    .desc-mobile {
        padding-top: 30px;
        font-family: "Poppins";
        font-style: italic;
        font-weight: 500;
        font-size: 40px;
        line-height: 54px;
        color: #10121f;
    }

    .desc-mobile {
        display: none;
    }

    .container-card-expert {
        .card-expert {
            padding-top: 50px;
            display: flex;

            .item {
                min-width: 33%;

                .card {
                    cursor: pointer;

                    .card-body {
                        padding: 0;

                        .icon {
                            object-fit: cover;
                            object-position: center;
                            background-position: center;
                            z-index: 1;
                            overflow: hidden;
                            transition: all .3s ease;

                            img {
                                transition: all .3s ease;
                                width: 100px;
                                height: 100%;
                            }
                        }

                        .icon:hover img {
                            transform: scale(1.03);
                        }
                    }
                }

                .discussion {
                    background: #e6d7f9;
                    border-radius: 20px;
                }

                .brainstorm {
                    background: #d7e5f9;
                    border-radius: 20px;
                    // margin-left: 40px;
                }

                .timeline {
                    background: #f9e3d7;
                    border-radius: 20px;
                    // margin-left: 40px;
                }

                .execution {
                    background: #e8f3d6;
                    border-radius: 20px;
                    // margin-left: 40px;
                }

                #collapse-discussion,
                #collapse-execution,
                #collapse-timeline,
                #collapse-brainstorm {
                    background: #222222;
                    box-shadow: 0px -2px 24px rgba(55, 46, 89, 0.2);
                    border-radius: 20px;
                    padding: 20px;
                    font-family: "Poppins";
                    color: #ffffff;

                    .btn-discussion,
                    .btn-execution,
                    .btn-timeline,
                    .btn-brainstorm {
                        font-weight: 700;
                        font-size: 20px;
                        line-height: 28px;
                        letter-spacing: 0.01em;
                    }

                    #collapseDiscussion,
                    #collapseBrainstorm,
                    #collapseTimeline,
                    #collapseExecution {
                        margin-top: 8px;
                        font-weight: 400;
                        font-size: 18px;
                        line-height: 26px;
                        letter-spacing: 0.01em;
                    }
                }
            }
        }

        .card-expert-mobile {
            display: none;

            .item {
                .card {
                    cursor: pointer;

                    .card-body {
                        padding: 0;

                        .icon {
                            object-fit: cover;
                            object-position: center;
                            background-position: center;
                            z-index: 1;
                            overflow: hidden;
                            transition: all .3s ease;

                            img {
                                transition: all .3s ease;
                                // width: 100px;
                                // height: 100%;
                            }
                        }

                        .icon:hover img {
                            transform: scale(1.03);
                        }
                    }
                }

                .discussion {
                    background: #e6d7f9;
                    border-radius: 20px;
                }

                .brainstorm {
                    background: #d7e5f9;
                    border-radius: 20px;
                    // margin-left: 40px;
                }

                .timeline {
                    background: #f9e3d7;
                    border-radius: 20px;
                    // margin-left: 40px;
                }

                .execution {
                    background: #e8f3d6;
                    border-radius: 20px;
                    // margin-left: 40px;
                }

                #collapse-discussion,
                #collapse-execution,
                #collapse-timeline,
                #collapse-brainstorm {
                    background: #222222;
                    box-shadow: 0px -2px 24px rgba(55, 46, 89, 0.2);
                    border-radius: 20px;
                    padding: 20px;
                    font-family: "Poppins";
                    color: #ffffff;

                    .btn-discussion,
                    .btn-execution,
                    .btn-timeline,
                    .btn-brainstorm {
                        font-weight: 700;
                        font-size: 20px;
                        line-height: 28px;
                        letter-spacing: 0.01em;
                    }

                    #collapseDiscussion,
                    #collapseBrainstorm,
                    #collapseTimeline,
                    #collapseExecution {
                        margin-top: 8px;
                        font-weight: 400;
                        font-size: 18px;
                        line-height: 26px;
                        letter-spacing: 0.01em;
                    }
                }
            }
        }

        .footer {
            padding-top: 200px;

            h1 {
                font-family: "Champ";
                font-weight: 900;
                font-size: 50px;
                line-height: 62px;
                letter-spacing: 0.02em;
                color: #10121f;
            }

            .title-mobile {
                display: none;
            }

            span {
                font-family: "Poppins";
                font-weight: 400;
                font-size: 18px;
                line-height: 30px;
                letter-spacing: 0.01em;
                color: #10121f;
            }

            .mobile {
                display: none;
            }
        }
    }
}

