        font-size: 58px;
        line-height: 72px;
        letter-spacing: 0.02em;
        color: #10121f;
    }

    #testi1 {
        background: linear-gradient(101.47deg, rgba(16, 18, 31, 0.6) 24.25%, rgba(49, 29, 105, 0.6) 95.04%);
        backdrop-filter: blur(7px);
        border-radius: 20px;
        padding: 40px;
        margin-top: 10px;
        opacity: 0;
        transition: all .5s ease;
        position: absolute;
        z-index: 2;

        .title {
            h1 {
                font-family: 'Champ';
                font-weight: 900;
                font-size: 32px;
                line-height: 38px;
                letter-spacing: 0.02em;
                color: #FFFFFF;
                margin-bottom: 0;
                margin-top: 0;
            }
        }

        .desc {
            font-family: 'Poppins';
            font-style: italic;
            font-weight: 500;
            font-size: 20px;
            line-height: 32px;
            letter-spacing: 0.01em;
            color: #FFFFFF;
            padding-top: 20px;
            padding-bottom: 20px;
        }

        .person {
            font-family: 'Poppins';
            font-style: italic;
            letter-spacing: 0.01em;
            color: #FFFFFF;
            line-height: 27px;

            h4 {
                font-weight: 700;
                font-size: 22px;
                margin-top: 0;
                margin-bottom: 8px;
            }

            span {
                font-weight: 400;
                font-size: 18px;
            }
        }
    }

    #testi1.active {
        transition: all .5s ease;
        display: block ;
        opacity: 1;
    }
}

